<template>
  <abstract-button
    class="button"
    :class="[typeClass, sizeClass, buttonInternalClass, shadowDepthClass ]"
    :style="buttonStyle" :type="nativeType" :href="href" :target="target" :disabled="disabled" :to="to" :tag="tag" :append="append" :replace="replace" :exact="exact"
  >
    <span class="button__label" :class="labelClass" v-if="label && labelPosition === 'before' && !circle">{{label}}</span>
    <icon class="button__icon" v-if="icon" :class="iconClass" :value="icon" size="xxs"></icon>
    <slot></slot>
    <span class="button__label" :class="labelClass" v-if="label && labelPosition === 'after' && !circle">{{label}}</span>
  </abstract-button>
</template>

<script>
import { getColor } from '@/utils';

import AbstractButton from './AbstractButton';
import Icon from '../icon';

export default {
  name: 'buttons',
  props: {
    //  append , 
    // :  /a  b,  append,  /b; ,  /a/b
    append: {
      type: Boolean,
    },
    // 
    backgroundColor: {
      type: String,
      default: '',
    },
    // 
    color: {
      type: String,
      default: '',
    },
    // . 
    circle: {
      type: Boolean,
      default: false,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // ""  inclusive match 
    exact: {
      type: Boolean,
      default: true,
    },
    //  100%
    fullWidth: {
      type: Boolean,
      default: false,
    },
    // a 
    href: {
      type: String,
      default: '',
    },
    // 
    icon: {
      type: String,
      default: '',
    },
    // ,  class 
    iconClass: {
      type: String,
      default: '',
    },
    // 
    label: {
      type: String,
    },
    // 
    labelPosition: {
      type: String,
      default: 'after',
    },
    //  class
    labelClass: {
      type: [String, Array, Object],
      default: '',
    },
    // button  type
    nativeType: {
      type: String,
    },
    primary: {
      type: Boolean,
      default: false,
    },
    secondary: {
      type: Boolean,
      default: false,
    },
    // 
    radius: {
      type: Boolean,
      default: false,
    },
    //  replace  router.replace(),  router.push(),  history 
    replace: {
      type: Boolean,
    },
    // 
    size: {
      type: String,
      default: 'md',   // sm, md, lg
    },
    // 
    shadow: {
      type: Boolean,
      default: false,
    },
    // 
    shadowDepth: {
      type: Number,
      default: 1,
    },
    // 
    type: {
      type: String,
      default: 'link',  // link: , flat: , ghost: 
    },
    // a  target 
    target: {
      type: String,
    },
    // 
    to: {
      type: [String, Object],
    },
    // 
    tag: {
      type: String,
      default: 'a',
    },
  },
  computed: {
    buttonInternalClass() {
      return {
        'button--primary': this.primary,
        'button--secondary': this.secondary,
        'button--disabled': this.disabled,
        'button--full': this.fullWidth,
        'button--shadow': this.shadow,
        'button--radius': this.radius,
        'button--circle': this.circle,
      };
    },
    // 
    typeClass() {
      return `button--type-${this.type}`;
    },
    // 
    sizeClass() {
      return this.size === 'md' ? '' : `button--size-${this.size}`;
    },
    // 
    shadowDepthClass() {
      return this.shadow ? `shadow--depth-${this.shadowDepth}` : '';
    },
    // 
    buttonStyle() {
      return {
        color: getColor(this.color),
        backgroundColor: getColor(this.backgroundColor),
      };
    },
  },
  components: {
    AbstractButton, Icon,
  },
};
</script>

<style lang="scss">
@import "../../style/import";

.button {
  user-select: none;
  outline: none;
  border: none;
  cursor: pointer;
  -webkit-appearance: none;
  margin: 0;
  font-size: 0;
  white-space: nowrap;
  font-weight: 400;
  display: inline-block;
  vertical-align: middle;
  height: $button-size-md;
  line-height: $button-size-md;
  padding: 0 px2rem(35px);
  text-align: center;
  border: 1px solid $button-color-transparent;
  color: $button-color-normal;
  background-color: $button-color-transparent;
  border-radius: px2rem(6px);
  transition: opacity 0.25s ease-in-out, color 0.25s ease-in-out;

  &:hover {
    opacity: 0.85;
  }

  // 
  &__icon {
    vertical-align: middle;
    + .button__label {
      margin-left: px2rem(8px);
    }
  }
  // 
  &__label {
    font-size: px2rem(32px);
    vertical-align: middle;
    + .button__icon {
      margin-left: px2rem(8px);
    }
  }

  // 
  &--primary {
    color: $button-color-primary;
  }
  // 
  &--secondary {
    color: $button-color-secondary;
  }
  // 
  &[disabled] {
    cursor: not-allowed;
    opacity: 0.45;
  }

  // 
  &--type-flat {
    border-color: $button-color-flat-normal;
    background-color: $button-color-flat-normal;

    &.button--primary {
      color: $button-text-color-abrupt;
      border-color: $button-color-primary;
      background-color: $button-color-primary;
    }
    &.button--secondary {
      color: $button-text-color-abrupt;
      border-color: $button-color-secondary;
      background-color: $button-color-secondary;
    }
  }

  // 
  &--type-ghost {
    border-color: $button-color-ghost-normal;
    background-color: $button-color-transparent;

    &.button--primary {
      border-color: $button-color-primary;
      background-color: $button-color-transparent;
    }
    &.button--secondary {
      border-color: $button-color-secondary;
      background-color: $button-color-transparent;
    }
  }

  // 
  &--full {
    width: 100%;
    padding: 0;
  }

  // 
  &--size-sm {
    height: $button-size-sm;
    line-height: $button-size-sm;
    padding: 0 px2rem(25px);

    .button__icon {
      font-size: px2rem(28px);

      + .button__label {
        margin-left: px2rem(6px);
      }
    }

    .button__label {
      font-size: px2rem(24px);

      + .button__icon {
        margin-right: px2rem(6px);
      }
    }
  }
  // 
  &--size-lg {
    height: $button-size-lg;
    line-height: $button-size-lg;
    padding: 0 px2rem(45px);

    .button__icon {
      font-size: px2rem(40px);
    }

    .button__label {
      font-size: px2rem(36px);
    }
  }

  // 
  &--radius {
    border-radius: 9999px;
  }

  // 
  &--circle {
    width: $button-size-md;
    padding: 0;
    border-radius: 9999px;

    &.button--size-sm {
      width: $button-size-sm;
    }

    &.button--size-lg {
      width: $button-size-lg;
    }
  }
}
</style>



