<template>
    <div class="tab-bar" :class="{ 'tab-bar--hidden': hidden}">
      <slot></slot>
    </div>
</template>

<script>
export default {
  name: 'tab-bar',
  props: {
    // 
    value: {
      type: String,
      default: '',
    },
    // 
    hidden: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
    };
  },
  methods: {
    handleItemClick(value) {
      // ,  value;
      //  value ,  change ,  value ;
      // :  this.value = value;
      if (value !== this.value) {
        this.$emit('change', value);
      }
    },
  },
};
</script>

<style lang="scss">
@import "../../style/import";

.tab-bar {
  height: $tab-bar-height;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: $tab-bar-background-color;
  border-top: 1px solid #e5e5e5;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: $zindex-bar;
  transition: bottom .2s, opacity .2s;

  &.tab-bar--hidden {
    opacity: 0;
    bottom: $tab-bar-height-hidden;
  }
}
</style>



