<template>
  <div class="page page-components">
    <panel-simple title="" wingBlank whiteSpace>
      <text-field
        label=""
        hintText=""
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        v-model="value"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        type="password"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        type="password"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        multiLine
      >
      </text-field>
    </panel-simple>

    <panel-simple title=", " wingBlank whiteSpace>
      <text-field
        label=""
        hintText=""
        labelFloat
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        labelFloat
        v-model="value"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        type="password"
        labelFloat
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        type="password"
        labelFloat
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        labelFloat
        multiLine
      >
      </text-field>
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <text-field
        hintText=""
      >
      </text-field>
      <text-field
        hintText=""
        v-model="value"
      >
      </text-field>
      <text-field
        hintText=""
      >
      </text-field>
      <text-field
        hintText=""
      >
      </text-field>
      <text-field
        hintText=""
        multiLine
      >
      </text-field>
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <text-field
        label=""
        hintText=""
        :maxLength="10"
        :errorText="inputErrorText"
        @textOverflow="handleInputOverflow"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        v-model="value"
        :maxLength="10"
        :errorText="inputErrorTextDefault"
        @textOverflow="handleInputOverflowDefault"
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        multiLine
        :maxLength="100"
        :errorText="multiLineInputErrorText"
        @textOverflow="handleMultiLineOverflow"
      >
      </text-field>
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <text-field
        label=""
        hintText=" - "
        disabled
      >
      </text-field>
      <text-field
        hintText=" - "
        disabled
      >
      </text-field>
      <text-field
        label=""
        hintText=" - "
        labelFloat
        disabled
      >
      </text-field>
      <text-field
        label=""
        hintText=""
        value=" - "
        disabled
      >
      </text-field>
      <text-field
        hintText=" - "
        disabled
      >
      </text-field>
      <text-field
        label=" - "
        hintText=""
        multiLine
        disabled
      >
      </text-field>
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <text-field
        label=""
        hintText=""
        :maxLength="10"
        :errorText="inputErrorTextEventHandle"
        @textOverflow="handleInputOverflowEventHandle"
        @focus="handleFocus"
        @blur="handleBlur"
        @change="handleChange"
        @input="handleInput"
      >
      </text-field>
    </panel-simple>
  </div>
</template>

<script>
import TextField from '@/components/textField';
import { PanelSimple } from '@/components/panel';

export default {
  name: 'c-text-field',
  components: {
    PanelSimple, TextField,
  },
  data() {
    return {
      value: '',
      inputErrorText: '',
      inputErrorTextDefault: '',
      multiLineInputErrorText: '',
      inputErrorTextEventHandle: '',
    };
  },
  methods: {
    handleInputOverflow(isOverflow) {
      this.inputErrorText = isOverflow ? '' : '';
    },
    handleInputOverflowDefault(isOverflow) {
      this.inputErrorTextDefault = isOverflow ? '10' : '';
    },
    handleMultiLineOverflow(isOverflow) {
      this.multiLineInputErrorText = isOverflow ? '100' : '';
    },

    handleInputOverflowEventHandle(isOverflow) {
      this.inputErrorTextEventHandle = isOverflow ? '' : '';
    },
    handleFocus(event) {
      console.log(' =>>', event);
    },
    handleBlur(event) {
      console.log(' =>>', event);
    },
    handleChange(event, value) {
      console.log(' =>>', value);
    },
    handleInput(event, value) {
      console.log(' =>>', value);
    },
  },
};
</script>



