/**
 *  item
 * @Created by  on 2017/4/30.
 */
export default {
  props: {
    //  append , 
    // :  /a  b,  append,  /b; ,  /a/b
    append: {
      type: Boolean,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // ""  inclusive match 
    exact: {
      type: Boolean,
      default: false,
    },
    // a 
    href: {
      type: String,
      default: '',
    },
    //  replace  router.replace(),  router.push(),  history 
    replace: {
      type: Boolean,
    },
    // a  target 
    target: {
      type: String,
    },
    // 
    to: {
      type: [String, Object],
    },
    // 
    tag: {
      type: String,
      default: 'a',
    },
  },
  methods: {
    getTagName() {
      const defaultTag = 'div';

      switch (true) {
        case !!this.to:
          return 'router-link';
        case !!this.href:
          return 'a';
        case !!this.containerElement:
          return this.containerElement;
        default:
          return defaultTag;
      }
    },
  },
  render(h) {
    const attrs = {
      disabled: this.disabled,
    };
    // a 
    if (this.href) {
      attrs.target = this.target;

      if (!this.disabled) {
        attrs.href = this.href;
      }
    }
    // 
    // : https://router.vuejs.org/zh-cn/api/router-link.html
    const props = this.to ? {
      to: this.to,
      tag: this.tag,
      activeClass: this.activeClass,
      exact: this.exact,
      append: this.append,
      replace: this.replace,
    } : {};

    return h(
      this.getTagName(),
      {
        attrs,
        props,
      },
      [
        this.$slots.default,
      ],
    );
  },
};



