<template>
  <abstract-item
    class="list-menu__item"
    :class="{ 'list-menu__item__line': line }" :href="href" :target="target" :disabled="disabled" :to="to" :tag="tag" :append="append" :replace="replace" :exact="exact"
  >
    <!-- ,  -->
    <div class="list-menu__item__thumb" v-if="showIcon">
      <slot name="thumb">
        <Icon v-if="iconName" :value="iconName" :color="iconColor" size="xs" />
      </slot>
    </div>

    <!--  -->
    <div class="list-menu__item__content">
      <!--  -->
      <div class="list-menu__item__title">
        <slot name="title">{{title}}</slot>
        <div class="list-menu__item__brief" v-if="brief">
          <slot name="brief">{{brief}}</slot>
        </div>
      </div>

      <!--  -->
      <div class="list-menu__item__extra">
        <slot name="extra"><span>{{extra}}</span></slot>
      </div>

      <!--  -->
      <div class="list-menu__item__arrow" v-if="hasArrow">
        <slot name="arrow">
          <i class="icon iconfont icon-arrow-right"></i>
        </slot>
      </div>

    </div>
  </abstract-Item>
</template>

<script>
// TODO: 
import Icon from '@/components/icon';
import AbstractItem from './AbstractItem';

export default {
  name: 'list-menu-item',
  props: {
    //  append , 
    // :  /a  b,  append,  /b; ,  /a/b
    append: {
      type: Boolean,
    },
    // 
    brief: {
      type: String,
      default: '',
    },
    // 
    border: {
      type: Boolean,
      default: true,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    extra: {
      type: String,
      default: '',
    },
    // ""  inclusive match 
    exact: {
      type: Boolean,
      default: false,
    },
    // 
    hasThumb: {
      type: Boolean,
      default: false,
    },
    //  arrow
    hasArrow: {
      type: Boolean,
      default: true,
    },
    // a 
    href: {
      type: String,
      default: '',
    },
    // 
    iconName: {
      type: String,
      default: '',
    },
    // 
    iconColor: {
      type: String,
    },
    // 
    line: {
      type: Boolean,
      default: false,
    },
    // 
    title: {
      type: String,
      default: '',
    },
    //  replace  router.replace(),  router.push(),  history 
    replace: {
      type: Boolean,
    },
    // a  target 
    target: {
      type: String,
    },
    // 
    to: {
      type: [String, Object],
    },
    // 
    tag: {
      type: String,
      default: 'a',
    },
  },
  computed: {
    //  class
    iconClassName() {
      return this.iconName ? `icon-${this.iconName}` : '';
    },
    //  class
    showIcon() {
      return this.iconName || this.hasThumb;
    },
  },
  components: {
    Icon, AbstractItem,
  },
};
</script>

<style lang="scss">
@import "../../style/import";

.list-menu__item {
  position: relative;
  overflow: hidden;
  display: flex;
  align-self: stretch;
  align-items: center;
  color: inherit;

  &:hover {
    color: inherit;
  }

  &:active {
    background-color: $list-menu-background-color-active;
  }

  // 
  &[disabled="disabled"] {
    .list-menu__item__thumb,
    .list-menu__item__title,
    .list-menu__item__extra,
    .list-menu__item__arrow {
      opacity: $list-menu-disabled-opacity;
    }

    &:active {
      background-color: $transparent;
    }
  }

  // 
  &.list-menu__item__line {
    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      border-bottom: 1px solid $list-menu-border-color;
      display: none;
    }
  }

  // , 
  .list-menu__item__thumb {
    padding-left: $space-blank-base;

    .icon {
      font-size: 1.2em;
    }

    img {
      display: block;
      width: px2rem(48px);
      height: px2rem(48px);
      vertical-align: middle;
    }
  }

  // 
  .list-menu__item__content {
    position: relative;
    min-height: px2rem(120px);
    padding: px2rem(20px) 0;
    margin-left: $space-blank-base;
    flex: 1;
    display: flex;
    align-self: stretch;
    align-items: center;
    max-height: 9999px;  // , 

    // 
    .list-menu__item__title {
      flex: 1;
      line-height: 1.4;

      span {
        vertical-align: middle;
      }
    }

    // 
    .list-menu__item__brief {
      color: $list-menu-text-color-brief;
      font-size: 0.8em;
    }
  }

  // 
  .list-menu__item__extra {
    color: $list-menu-text-color-extra;
    font-size: 0.8em;
    text-align: right;
    padding: 0 $space-blank-sm 0 $space-blank-base;

    span {
      vertical-align: middle;
    }
  }

  // 
  .list-menu__item__arrow {
    padding-right: $space-blank-base;
    color: $list-menu-text-color-arrow;
    font-size: 0.8em;
  }
}
</style>



