import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters([
      'auth',
      'isLogged',
    ]),
    //  token
    requestAccessToken() {
      return {
        accesstoken: this.auth ? this.auth.token : '',
      };
    },
  },
  created() {
    // 
    this.getAuth();
  },
  methods: {
    ...mapActions([
      'getAuth',
    ]),
  },
};



