import { mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters([
      'messageCount',
      'isGetMessageCounted',
      'isGetMessageCounting',
    ]),
  },
  created() {
    //  &&  &&  token , 
    if (!this.isGetMessageCounting && !this.isGetMessageCounted && this.auth && this.auth.token) {
      this.getMessageCount(this.requestAccessToken);
    }
  },
  methods: {
    ...mapActions([
      'getMessageCount',
    ]),
  },
};



