<template>
  <div class="page page-components">
    <panel-simple title="" wingBlank whiteSpace>
      <checkbox class="demo-checkbox" label=""/><br />
      <checkbox class="demo-checkbox" label="icon" uncheckIcon="message" checkIcon="message-fill"/><br />
      <checkbox class="demo-checkbox" label="icon" uncheckIcon="user" checkIcon="user-fill"/><br />
      <checkbox class="demo-checkbox" label="" labelLeft/><br />
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <checkbox class="demo-checkbox" label="" disabled/><br />
      <checkbox class="demo-checkbox" label="" disabled :value="true"/><br />
      <checkbox class="demo-checkbox" label="ICON" disabled uncheckIcon="message" checkIcon="message-fill"/><br />
      <checkbox class="demo-checkbox" label="ICON" disabled uncheckIcon="message" checkIcon="message-fill" :value="true" /><br />
      <checkbox class="demo-checkbox" label="" labelLeft disabled/><br />
      <checkbox class="demo-checkbox" label="" labelLeft disabled :value="true"/><br />
    </panel-simple>

    <panel-simple title="" wingBlank whiteSpace>
      <checkbox class="demo-checkbox" name="group" v-model="list" value="checkbox111" nativeValue="checkbox1" label=" 1"/><br />
      <checkbox class="demo-checkbox" name="group" v-model="list" value="checkbox222" nativeValue="checkbox2" label=" 2"/><br />
      <checkbox class="demo-checkbox" name="group" v-model="list" value="checkbox333" nativeValue="checkbox3" label=" 3"/><br />
      <div class="demo-checkbox">{{ list }}</div>

      <checkbox :label="single ? 'true' : 'false'" v-model="single"/>
    </panel-simple>
  </div>
</template>

<script>
import Checkbox from '@/components/checkbox';
import { PanelSimple } from '@/components/panel';

export default {
  name: 'c-checkbox',
  data() {
    return {
      list: [],
      single: false,
    };
  },
  components: {
    Checkbox, PanelSimple,
  },
};
</script>

<style>
.demo-checkbox {
  margin-bottom: 0.25rem;
}
</style>



